.contacts__title, .price__title, .gallery__title {
  font-family: "Caveat";
  font-size: 24px;
  color: maroon;
  text-align: center;
}

.footer__social-link, .footer__contacts-link, .header__info-btn, .nav-link {
  position: relative;
}
.footer__social-link:hover, .footer__contacts-link:hover, .header__info-btn:hover, .nav-link:hover {
  color: inherit;
  border-radius: 5px;
  background-color: rgba(128, 0, 128, 0.1);
  backdrop-filter: blur(3px);
  -webkit-backdrop-filter: blur(3px);
}
.footer__social-link:hover::after, .footer__contacts-link:hover::after, .header__info-btn:hover::after, .nav-link:hover::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  background-color: maroon;
  height: 2px;
  width: 100%;
}

@font-face {
  font-family: "Caveat";
  src: url("../fonts/Caveat-Medium.ttf") format("truetype");
}
@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Regular.ttf") format("truetype");
}
html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

ul[class],
ol[class] {
  padding: 0;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
  font-size: inherit;
}

ul[class] {
  list-style: none;
}

img {
  max-width: 100%;
  display: block;
}

input,
button,
textarea,
select {
  font: inherit;
}

a {
  text-decoration: none;
}

html,
body {
  height: 100%;
  font-weight: 400;
  font-size: 16px;
  line-height: 28px;
  color: maroon;
  font-family: "Roboto";
  overflow-x: hidden;
  min-width: 300px;
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}

.main {
  flex: 1 0 auto;
  color: #8B008B;
}

.footer {
  flex: 0 0 auto;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
}

.container-fluid {
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 10px;
}

.header {
  background-color: #fed0d2;
}
.header__inner {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-start;
  height: 620px;
}

.logo_img {
  position: relative;
  display: block;
  height: 620px;
  object-fit: cover;
  position: absolute;
  top: 0;
  right: 0px;
  z-index: 1;
}

.navbar {
  position: relative;
  z-index: 2;
  width: 100%;
}

.nav {
  display: flex;
  margin-top: 20px;
}
.nav-item {
  padding: 0px 20px;
}
.nav-link {
  font-family: "Caveat";
  color: maroon;
  font-size: 22px;
}

.header__info {
  display: flex;
  width: 100%;
  flex-direction: column;
  position: absolute;
  bottom: 40px;
  z-index: 2;
  color: maroon;
  font-family: "Roboto";
  padding: 10px 15px 10px 35px;
  border-radius: 5px;
  background-color: rgba(128, 0, 128, 0.1);
  backdrop-filter: blur(3px);
  -webkit-backdrop-filter: blur(3px);
}
.header__info-title {
  margin-left: 35px;
  font-size: 18px;
}
.header__info-text {
  margin: 10px 0;
  font-size: 56px;
  font-family: "Caveat";
  color: inherit;
}
.header__info-btn {
  margin: 5px;
  background-color: transparent;
  max-width: 220px;
}
.header__info-btn a {
  color: maroon;
}

.footer {
  background-color: #fed0d2;
}
.footer__inner {
  height: 120px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.footer__social-list {
  margin: 30px 0;
  display: inline-block;
}
.footer__social-link, .footer__contacts-link {
  padding: 0 10px;
  font-family: "Caveat";
  font-size: 22px;
  color: maroon;
  padding-left: 28px;
  position: relative;
}
.footer__social-link::before, .footer__contacts-link::before {
  content: "";
  position: absolute;
  left: 0;
  top: 8px;
  width: 16px;
  height: 16px;
}
.footer__social-link--facebook::before {
  background-image: url(../img/facebook-square.svg);
  width: 17px;
  height: 20px;
}
.footer__social-link--instagram::before {
  background-image: url(../img/instagram.svg);
  width: 18px;
  height: 20px;
}
.footer__contacts-link--mail::before {
  background-image: url(../img/envelope.svg);
  width: 20px;
  height: 20px;
}

.about .container,
.contacts .container {
  background-color: #FFE4B5;
  margin-top: 50px;
}

.about__inner-title {
  text-align: center;
  color: maroon;
  margin-top: 20px;
  font-size: 24px;
  font-family: "Caveat";
}
.about__inner-text {
  font-size: 16px;
}
.about__img img {
  display: block;
  max-height: 600px;
  height: 100%;
  max-width: 600px;
  width: 100%;
}

.gallery .container,
.price .container {
  background-color: #fed0d2;
  margin-top: 50px;
}

.carousel-item img {
  max-height: 700px;
  margin: 0 auto;
}

.contacts_item {
  margin: 25px;
}
.contacts_item a {
  text-decoration: none;
  color: maroon;
  padding: 10px;
  border-radius: 10px 0 10px 0;
}

@media (max-width: 992px) {
  .nav-link {
    position: relative;
  }
  .nav-link:hover::after {
    width: 90px;
  }
  .navbar {
    width: max-content;
  }
}
@media (max-width: 540px) {
  .navbar-nav {
    max-width: 100px;
    border-radius: 5px;
    background-color: rgba(128, 0, 128, 0.1);
    backdrop-filter: blur(3px);
    -webkit-backdrop-filter: blur(3px);
  }
  .nav {
    display: flex;
  }
  .nav-item {
    padding: 0 5px;
  }
}